<template>
  <div class="app-container">
    <el-card>
      <div class="main-title">
        <span>分组名称：{{ name }}</span>
        <span>分销成员数量：{{ total }}</span>
      </div>
    </el-card>
    <!-- <el-card class="operate-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="积分来源：">
              <el-select v-model="value" placeholder="默认选择全部" clearable>
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
          </el-form-item>
          <el-form-item label="起止时间：">
            <el-date-picker v-model="list.time"
                            value-format="yyyy-MM-dd"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card> -->
    <div class="table-container">
      <el-table :data="tableList"
                @selection-change="handleSelectionChange"
                style="width: 100%"
                ref="multipleTable">
        <el-table-column type="selection"
                         width="55">
        </el-table-column>
        <el-table-column label="分销员昵称"
                         align="center">
          <template slot-scope="scope">{{scope.row.channelUsernickName}}</template>
        </el-table-column>
        <el-table-column label="分销员手机号"
                         prop="channelUserPhone"
                         align="center">
        </el-table-column>
        <el-table-column label="邀请方昵称"
                         prop="inviternickName"
                         align="center">
        </el-table-column>
        <el-table-column label="邀请方手机号"
                         prop="inviterPhone"
                         align="center">
        </el-table-column>
        <el-table-column label="操作"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       @click="changeGroup(scope.row)">修改分组</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog title="修改用户分组"
               :visible.sync="dialogFormVisible">
      <el-form>
        <el-form-item label="按分组查看：">
          <el-select v-model="list.groupId"
                     placeholder="默认分组">
            <el-option v-for="item in groupList"
                       :key="item.id"
                       :label="item.name"
                       :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="setDistriGroup()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 固定在下面 -->
    <div class="flex-row-between"
         style="margin-top:20px">
      <div>
        <el-button @click="changeGroup()"
                   type="primary">
          批量修改分组
        </el-button>
        <el-button @click="goBack()"
                   type="primary">
          返回上一页
        </el-button>
      </div>

      <!-- 页码 -->
      <el-pagination background
                     style="margin-top: 10px;margin-left:20px"
                     @size-change="handleSizeChange"
                     @current-change="pageChange"
                     layout="total,sizes,prev,pager,next,jumper"
                     :page-size="page_size"
                     :page-sizes="[10, 20, 30]"
                     :current-page="pageNum"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import {
  groupCapacity,   //检查分组人数
  initChannelGroupList,//查看店铺分组
  findChannelUserByGroupId,   //查看分组下的分销员
  changeChannelUserGroup,  //修改分组
} from '@/api/distributionManagement/distributor'
export default {
  data() {
    return {
      list: {
        groupId: '',
      },
      id: this.$route.query.id,
      name: this.$route.query.name,
      tableList: [],
      multipleSelection: [],
      groupList: [],
      ids: [],
      dialogFormVisible: false,
      pageNum: 1,
      page_size: 15,
      total: 0,
    }
  },
  created() {
    this.searchGroupList()
    this.searchList()
  },
  mounted() {

  },
  methods: {
    //查询分销员分组
    searchGroupList() {
      initChannelGroupList({
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.groupList = res.data
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    //查询数据列表
    searchList() {
      findChannelUserByGroupId({
        shopId: this.shop.shopId,
        groupId: this.id,
        pageNum: this.pageNum,
        pageSize: this.page_size,
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
        } else {
          this.$notify.error({
            title: '失败',
            message: res.message
          })
        }
      })
    },

    handleSelectionChange(value) {
      this.multipleSelection = value;
      console.log(this.multipleSelection, '多选')
    },

    //修改分组
    changeGroup(row) {
      console.log(row)
      this.ids = []
      if (!row && this.multipleSelection.length == 0) {
        this.$message({
          message: '请选择要操作的申请',
          type: 'warning',
          duration: 1000
        });
        return;
      }
      if (row) {
        this.ids.push(row.cUserId)
      } else {
        this.ids = this.multipleSelection.map(item => item.cUserId)
      }
      this.dialogFormVisible = true
      let obj = this.groupList.find(item =>{
        return item.isDefault == 1
      })
      this.list.groupId = obj.id
    },

    //批量修改分组
    setDistriGroup() {
      if(this.list.groupId == ''){
        this.$notify.error({
          title: '失败',
          message: '请选择分组'
        })
      }
      groupCapacity({
        shopId: this.shop.shopId,
        groupId: this.list.groupId,
        count: this.ids.length
      }).then(res => {
        if (res.data == true) {
          changeChannelUserGroup({
            shopId: this.shop.shopId,
            groupId: this.list.groupId,
            ids: this.ids
          }).then(res => {
            console.log(res)
            if (res.data == true) {
              this.$notify({
                title: '成功',
                type: 'success',
                message: res.message
              })
              this.dialogFormVisible = false
              this.searchList()
            } else {
              this.$notify.error({
                title: '失败',
                message: res.message
              })
            }
          })
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          })
        }
      })
    },
    //返回
    goBack() {
      this.$router.push({
        path: '/distributionManagement/distributor',
        query: {
          activeName: 'four'
        }
      })
    },

    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="scss" scoped>
.app-container {
  padding: 20px 20px 20px 20px;
  .main-title {
    display: flex;
    justify-content: space-around;
    color: #f59a23;
    // span{
    //   flex:1
    // }
  }
}
.fixedB {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}
</style>